import Util from '@/common/util.js';
import uniList from '@/components/uni-list/uni-list.vue';

export default {
	components: {
		uniList
	},
	data() {
		return {
			userList: [],
			showBnt: false
		}
	},
	onLoad() {
		//判断是否登录
		if(!Util.isLogin()){
			uni.redirectTo({
				url: '/pages/index/index'
			});
			return;
		}
		//判断是否注册过用户信息
		let id = getApp().globalData.users[0].id;
		if(id == 0){
			uni.showToast({
				title: "请先注册用户信息",
				image: "../../static/info-icon.png"
			});
			setTimeout(function(){
				uni.navigateTo({
					url: '/pages/user/user?userIndex=0'
				});
			},1500);
		}
	},
	onShow: function() {
		this.userList = JSON.parse(JSON.stringify(getApp().globalData.users));
		if (!getApp().globalData.isNewUser) {
			this.showBnt = true;
		}
	},
	methods: {
		updateUser(e, v) {
			uni.navigateTo({
				url: '/pages/user/user?userIndex=' + v
			});
		},
		addMember() {
			uni.navigateTo({
				url: '/pages/user/user?userIndex=1'
			});
		}
	}
}
